<template>
	<view>
		<view class="about-card">
			<view class="post">
				<view class="head">
					<view class="head-text">{{options.title}}</view>
				</view>
				<view class="content">
					<view class="title-text">{{options.subTitle}}</view>
					<view class="content-text">{{options.content}}</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: 'CampCard',
		props: {
			options: {
				title: {
					type: String,
					default: 'unknown'
				},
				subTitle: {
					type: String,
					default: 'unknown'
				},
				content: {
					type: String,
					default: 'unknown'
				},
			},
		},
	}
</script>

<style>
	@import './about-card.css';
</style>
